import React from 'react'
import s from './News.module.css'

const News = ({ newsPosts }) => {
    console.log(newsPosts)
    let posts = newsPosts.map(post => {
        return (
            <div className={s.post} key={post[0].id}>
                <div className={s.title}>{post[0].text}</div>
                <div className={s.discription}>{post[1].text}</div>
                <div>
                    <img className={s.image} src={post[2].text} alt='failing load' />
                </div>
            </div>
        )
    })

    return (
        <div>
            {posts}
        </div>
    )
}

export default News